@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
header 
{
    height :100vh ;

    overflow: hidden;
    
}
.header__container 
{
    text-align: center;
    height: 100%;
    position: relative;
}

/* **************CTA************** */

.cta{
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}
.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color--primary);
}
.me
{
    background: linear-gradient(var(--color--primary),transparent);
    width: 22rem;
    height:  26rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
   padding: 3rem 1rem 1rem 1rem; 
   overflow: hidden;
}
.scroll__down
{
    position: absolute;
    bottom: 5rem;
    right: -2.3rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-size: 0.9rem;
    
}

/* ============MEDIA QUERUES (Medium size) =============  */


@media screen and (max-width : 1024px)
{

 header  {
    height:68vh;}
   
}


@media screen and (max-width : 660px)
{

    header{
        height: 100vh;
    }
    .header__socials,.scroll__down
    {
        display: none;
    }
   
}
footer{
    background: var(--color--primary);
    padding: 3rem 0 ;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}
footer a {
    color: var(--color-bg);
}

.footer__logo{
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}
.premalinks{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 0 auto 3rem;
}
.footer__socials{
    display:flex;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 4rem;
}
.footer__socials a{
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}
.footer__socials a:hover{
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}
.footer__copyright{
    margin-bottom: 4rem;
    color: var(--color-bg);
}
.port__container{
    display:grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap:2.5rem;
    gap:2.5rem;
}
.port__item{
    background: var(--color-bg-variant);
    padding: 1.3rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}
.port__item:hover{
    border-color: var(--color-primary-variant);
    background: transparent;
}
.port__item-image{
    border-radius: 1.5rem;
    overflow: hidden;
}
.port__item h3{
    margin: 1.2rem 0 2rem;
}
.port__item-cta{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
}
@media screen and (max-width: 1024px){
    .port__container{
        grid-template-columns: 1fr 1fr;
        grid-gap:1.2rem;
        gap:1.2rem;
    }
}
@media screen and (max-width: 600px){
    .port__container{
        grid-template-columns: 1fr ;
        grid-gap:1rem;
        gap:1rem;
    }
}
.experience__container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}
h5{
    margin: 1rem 0rem 0rem;
}

.experience__container > div{
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}
.experience__container > div:hover{
    background:transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}
.experience__container >div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color--primary);
}
.experience__content{
    grid-template-columns: 1fr 1fr;
    display:grid;
    grid-row-gap: 2rem;
    row-gap: 2rem;

}
.experience__details{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}
.experience__details-icon
{
    margin-top: 6px;
    color: var(--color--primary);
}


@media screen and (max-width:1024px)
{
    .experience__container{
        grid-template-columns: 1fr;
    }
    .experience__container > div{
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }
}
@media screen and (max-width:600px){
    .experience__container{
        grid-gap:1rem;
        gap:1rem;
    }
    .experience__container > div{
        width: 100%;
        padding: 2rem 1rem;
    }
}

.container.contact__container{
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap: 12%;
    gap: 12%;
}
.contact__options{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem ;
    gap: 1.2rem ;
}
.contact__option{
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);

}
.contact__option:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
}
.contact__option-icon{
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}
.contact__option a {
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}

form{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

input,textarea{
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: var(--color-white);
}

@media screen and (max-width:1024px) {
    .container.contact__container{
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
    
}
@media  screen and ( max-width:600px) {
    .container.contact__container{
        width: var(--container-width-sm);
    }
    .perbtn{
       justify-content: center;
    }
}
.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap : 15%;
    gap : 15%;
}
.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background:linear-gradient(45deg,transparent,var(--color--primary),transparent);
    display:grid;
    place-items: center;
}
.about__me-image{
    aspect-ratio: 1/1;
    border-radius: 2rem;
    height: cover;
    overflow: hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    transition: var(--transition);
}

.about__me-image:hover{
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
.about__cards{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 1rem;
    gap: 1rem;
    margin: 1rem;
}
.about__card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}
.about__card:hover{
    background:transparent;
    border-color: var(--color--primary);
    border-width: 3px;
}   
.about__icon{
    color: var(--color--primary);
    font-size: 1.4rem;

    margin-bottom: 1rem;

}
.about__card h5 {
    font-size: 1rem;
}
.about__card small {
    font-size: 0.7rem;
}
.about__content{
text-align: center;
}
.about__content p {
    margin: 1rem 1rem 2rem;
color: var(--color--light);
}

.firstWord{
    color: var(--color--primary);
    font-size: 32px;
}
.MyName{
    color: var(--color--primary);
    font-weight:bold;
}



/* ============MEDIA QUERUES (Medium size) =============  */

@media screen and (max-width : 1024px)
{

 .about__container  {
  grid-template-columns: 1fr;
grid-gap: 0;
gap: 0;}
.about__me{
    width: 50%;
    margin:2rem auto 4rem ;
}
.about__content{
    margin: 1rem 9 1.5rem;
    text-align: center;

}
   
}
.P::first-word{
    color:red;
}

@media screen and (max-width : 660px)
{
    .about__me{
        width: 50%;
        margin:0 auto 3rem ;
        
    }
    .about__cards{
        grid-template-columns: 1fr 1fr;
    }
    .about__content  {
        text-align: center;
    }
    .about__content p  {
        margin:1.5rem 0;
    }
    
}

nav{
    background: rgba(0,0,0,0.3);
    content: max-content;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}
nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}
nav a:hover
{
    background:rgba(0,0,0,0.4);
}
nav a.active{
    background: var(--color-bg);
    color: var(--color-white );
}

*{
    margin : 0 ; 
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;

}
:root{
    --color-bg : #1f1f38;
    --color-bg-variant :#2c2c6c ;
    --color--primary :#4db5ff ;
    --color-primary-variant :rgba(77,181,255,0.4) ;
    --color-white :#fff ;
    --color-light:rgba(255,255,255,0.6) ;  

    --transition: all 400ms ease ;

    --container-width-lg : 75%;
    --container-width-md: 86%;
    --container-width-sm:90% ;
    }

    html{
        scroll-behavior : smooth;

    }

    ::-webkit-scrollbar{
        display : none ; 
    }
body {
    font-family : "Poppins",sans-serif;
    background:#1f1f38;
    background:var(--color-bg) ;
    color : #fff;
    color : var(--color-white);
    background-image : url(data:image/png;base64,);

}

/* ============GENERAL STYLES  =============== */

.container {
    width: 75%;
    width: var(--container-width-lg);
    margin : 0 auto  ;
}
h1,
h2,
h3,
h4,
h5{
    font-weight: 500;
}

h1{
    font-size: 2.5rem;
}


section>h2, section > h5 
{
    text-align: center;
    color: rgba(255,255,255,0.6);
    color: var(--color-light);

}  

section>h2{
    color : #4db5ff;
    color : var(--color--primary);
    margin-bottom: 3rem;
}

.text-light{
    color : rgba(255,255,255,0.6);
    color : var(--color-light) ; 

}

a{
color : #4db5ff;
color : var(--color--primary);
transition: all 400ms ease;
transition: var(--transition);
}

a:hover
{
    color : #fff;
    color : var(--color-white);

}


.btn{
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color : #4db5ff;
    color : var(--color--primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border : 1px solid ;
    transition: all 400ms ease;
    transition: var(--transition);

}
.btn:hover{
    background: #fff;
    background: var(--color-white);
    color:#1f1f38;
    color:var(--color-bg);
    border-color : transparent;
}

.btn-primary
{
    background:#4db5ff;
    background:var(--color--primary);
    color: #1f1f38;
    color: var(--color-bg);

}

img {
    -webkit-mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1.0) 80%, transparent 100%);
            mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1.0) 80%, transparent 100%);

        display: block;
    width:100% ;
    object-fit: cover;
}


/* ============MEDIA QUERUES (Medium size) =============  */


@media screen and(max-width : 1024px)
{
    .container
    {
        width : 75%;
        width : var(--container-width-lg);

    }
    section {
        margin-top: 6rem;
    }
}

/* ============MEDIA QUERUES (Small size) =============  */


@media screen and(max-width : 600px)
{
    .container
    {
        width : 90%;
        width : var(--container-width-sm);

    }
    section {
        margin-top: 2rem;
    }
}

